body {
  font-family: "Roboto", sans-serif;
  font-size: 1.8rem;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
}

main {
  flex: 1;
}

.swal2-popup {
  font-size: 2rem !important;
}

h1, h2, h3 {
  font-family: "Roboto", sans-serif;
  font-weight: 900;
  margin: 2rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.text-center {
  text-align: center;
}

[class$=__heading] {
  font-family: "Roboto", sans-serif;
  font-weight: 900;
  margin: 2rem 0;
  margin: 5rem 0 3rem 0;
  text-align: center;
}

[class$=__descripcion] {
  text-align: center;
  font-size: 2rem;
  font-weight: 400;
  color: #64748B;
  margin-bottom: 5rem;
}

.main {
  background-color: #f3f4f6;
}
.main__contenedor {
  width: min(95%, 140rem);
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .main__contenedor {
    flex-direction: row;
    justify-content: space-evenly;
    align-items: flex-start;
    padding: 4rem 0;
  }
}
.main__contenido {
  text-align: center;
}
@media (min-width: 768px) {
  .main__contenido {
    text-align: left;
  }
}
.main__contenido--titulo {
  font-size: 4rem;
  color: #111827;
  margin-bottom: 1rem;
}
.main__contenido--texto {
  font-size: 2.5rem;
  color: #000000;
  margin-bottom: 2rem;
}
.main__contenido--boton {
  display: inline-block;
  background-color: #2563eb;
  color: #FFFFFF;
  padding: 1rem 4rem;
  border-radius: 0.5rem;
  font-size: 2rem;
  font-weight: 700;
  text-decoration: none;
  transition: background-color 0.3s ease;
}
.main__contenido--boton:hover {
  background-color: #1e40af;
}
.main img {
  box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
  max-width: 70rem;
  border-radius: 0.75rem;
}

.somos {
  width: min(95%, 80rem);
  margin: 0 auto;
  font-size: 3rem;
  margin-top: 2rem;
  margin-bottom: 2rem;
}
.somos__texto {
  font-size: 2.5rem;
  text-align: center;
}

.modulos {
  padding: 4rem 2rem;
  max-width: 1000px;
  margin: 0 auto;
}
.modulos__titulo {
  text-align: center;
  font-size: 3rem;
  font-weight: 700;
  color: #111827;
  margin-bottom: 2rem;
}
.modulos__grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .modulos__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .modulos__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}

.modulo {
  background-color: #FFFFFF;
  border: 1px solid #caccd1;
  border-radius: 1rem;
  padding: 1.5rem 1rem;
  width: 100%;
  text-align: center;
}
.modulo:hover {
  cursor: pointer;
  box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
}
.modulo__icono {
  margin-bottom: 1rem;
}
.modulo__icono img {
  width: 64px;
  height: 64px;
}
.modulo__nombre {
  font-size: 2rem;
  font-weight: 600;
  color: #1f2937;
  margin-bottom: 0.5rem;
}
.modulo__descripcion {
  font-size: 1.5rem;
  color: #6b7280;
}

.auth {
  width: min(95%, 60rem);
  margin: 0 auto;
  margin-top: 8rem;
}

.body {
  background-color: #f3f4f6;
}

.dashboard {
  width: min(95%, 140rem);
  margin: 0 auto;
  padding: 2rem;
}
.dashboard__flex {
  display: flex;
  min-height: 100vh;
}
.dashboard__stats {
  display: flex;
  gap: 1.5rem;
  margin-bottom: 2rem;
  flex-wrap: wrap;
}
.dashboard__stats--stat {
  flex: 1;
  min-width: 200px;
  padding: 1.5rem;
  background: #FFFFFF;
  border-radius: 12px;
  box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
  text-align: center;
}
.dashboard__stats--stat h3 {
  margin-bottom: 0.3rem;
  font-weight: 900;
}
.dashboard__stats--stat p {
  color: #000000;
}
.dashboard__stats--stat-alert h3 {
  color: #c82222;
}
.dashboard__stats--stat-warning h3 {
  color: #f59e0b;
}
.dashboard__tabla {
  background: #FFFFFF;
  border-radius: 1.2rem;
  padding: 2rem;
  box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
}
.dashboard__tabla h2 {
  margin-bottom: 1rem;
  font-size: 2.5rem;
}
.dashboard__tabla table {
  width: 100%;
  border-collapse: collapse;
  font-size: 2.5rem;
}
.dashboard__tabla table th {
  padding: 0.75rem;
  text-align: left;
  color: #000000;
  font-weight: 700;
  border-bottom: 1px solid #f3f4f6;
  font-size: 2rem;
}
.dashboard__tabla table td {
  font-size: 1.8rem;
  padding: 1rem;
  color: #000000;
}
.dashboard__tabla table tr {
  border-top: 1px solid #f3f4f6;
}

.categorias {
  margin-top: 7rem;
}
.categorias__heading {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}
.categorias__lista {
  width: min(95%, 140rem);
  margin: 0 auto;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .categorias__lista {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .categorias__lista {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1200px) {
  .categorias__lista {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(4, 1fr);
  }
}

.categoria {
  box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 2rem;
  background-color: #FFFFFF;
  padding: 1.5rem;
  border-radius: 0.5rem;
  border: 1px solid #f3f4f6;
}
.categoria__nombre {
  font-weight: 500;
  color: #000000;
}
.categoria__eliminar {
  background-color: #c82222;
  color: #FFFFFF;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 1rem;
  font-size: 1.6rem;
  cursor: pointer;
}
.categoria__eliminar:hover {
  background-color: #c82222;
}

.sidebar {
  background: linear-gradient(to bottom, #1f2937, #111827);
  color: #FFFFFF;
  width: 25rem;
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.sidebar__logo {
  color: #FFFFFF;
  text-align: center;
  margin-bottom: 2rem;
  white-space: nowrap;
  transition: color 0.5s ease;
}
@media (min-width: 1024px) {
  .sidebar__logo {
    text-align: left;
    margin: 0;
  }
}
.sidebar__logo:hover {
  color: #caccd1;
}
.sidebar__nav {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.sidebar__nav--contenedor {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem 0.5rem;
  border-radius: 0.5rem;
  transition: background 0.2s;
}
.sidebar__nav--contenedor:hover, .sidebar__nav--contenedor__active {
  background: #374151;
  color: #fff;
  cursor: pointer;
}
.sidebar__nav img {
  width: 3rem;
}
.sidebar__link {
  color: #d1d5db;
  text-decoration: none;
  font-size: 2rem;
}

.panel {
  padding: 2rem;
}
.panel__heading {
  font-size: 3rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
  text-align: center;
}
.panel__texto {
  text-align: center;
  color: #000000;
  margin-bottom: 2rem;
}

.header {
  width: 100%;
  background-color: #f3f4f6;
}
.header__contenedor {
  width: min(95%, 140rem);
  margin: 0 auto;
  display: flex;
  align-items: center;
  gap: 2rem;
  flex-direction: column;
  padding: 4rem 0;
}
@media (min-width: 768px) {
  .header__contenedor {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    gap: 0;
  }
}
.header__contenedor img {
  width: 25rem;
}

.navegacion {
  display: flex;
  gap: 2rem;
}
.navegacion__enlace {
  all: unset;
  color: #000000;
  font-size: 1.8rem;
  text-transform: uppercase;
  transition-property: color;
  transition-timing-function: ease-in;
  transition-duration: 0.3s;
  font-weight: 700;
}
.navegacion__enlace:hover {
  cursor: pointer;
  color: #2563eb;
}

.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.footer__copy {
  margin: 5rem 0 1rem 0;
  font-size: 1.5rem;
}
.footer__dev {
  margin: 0;
  text-align: center;
  background-color: #2563eb;
  width: 100%;
  padding: 1rem 0;
  font-size: 1.5rem;
  color: #FFFFFF;
}
.footer__dev a {
  all: unset;
  color: #000000;
}
.footer__dev a:hover {
  cursor: pointer;
  color: #FFFFFF;
}

.alerta {
  width: min(95%, 60rem);
  margin: 0 auto;
  padding: 1.5rem 1.5rem 1.5rem 3rem;
  text-transform: uppercase;
  margin-bottom: 3rem;
  font-size: 1.2rem;
  font-weight: 900;
}
.alerta__error {
  background-color: #f8d7d7;
  border-left: 0.5rem solid #c82222;
  color: #c82222;
}
.alerta__exito {
  background-color: #ebfbf1;
  border-left: 0.5rem solid #22c55e;
  color: #22c55e;
}

.formulario {
  display: flex;
  flex-direction: column;
}
.formulario__productos {
  width: min(95%, 60rem);
  margin: 0 auto;
  box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
  padding: 3rem 2rem;
  border-radius: 1rem;
  background-color: #FFFFFF;
}
.formulario__fieldset {
  border: none;
  margin-top: 1rem;
  padding: none;
}
.formulario__legend {
  color: #caccd1;
  font-size: 2.2rem;
  margin-bottom: 2rem;
}
.formulario__campo {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin: 1rem 0;
}
.formulario__label {
  display: block;
  font-size: 1.8rem;
  font-weight: 700;
  color: rgba(0, 0, 0, 0.8);
}
.formulario__input, .formulario__select {
  padding: 1rem;
  border: 1px solid #9397a2;
  border-radius: 0.5rem;
}
.formulario__input--file, .formulario__select--file {
  border: none;
}
.formulario__radio {
  display: flex;
  gap: 2rem;
}
.formulario__texto {
  margin-top: 3rem;
}
.formulario__listado {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  margin-top: 1.5rem;
}
.formulario__listado > li {
  background-color: #2563eb;
  padding: 1rem;
  border-radius: 0.5rem;
  font-size: 1.7rem;
  color: #FFFFFF;
  font-weight: 700;
}
.formulario__submit {
  background-color: #2563eb;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  width: auto;
  align-self: flex-start;
  border-radius: 0.5rem;
}
.formulario__submit:hover {
  background-color: #134cca;
  cursor: pointer;
}
.formulario__submit--registrar, .formulario__submit--full {
  width: 100%;
}

.acciones, .acciones--centrar {
  margin-top: 3rem;
  display: flex;
  justify-content: space-between;
}
.acciones__enlace {
  color: #000000;
  font-size: 1.7rem;
  font-weight: 300;
}
.acciones--centrar {
  justify-content: center;
}